import React from "react";
import { useState } from "react";

const Commands = ()=>{
    const [count, setCount] = useState(0)
    const [display, setDisplay] = useState(false)
    const [commands, setCommands] = useState("")
    const [newVariabless, setNewVariabless] = useState("")

    const likeFunc =( ) =>{
        setCount(count + 1)
    }
    const comandDunc = ()=>{
        setDisplay(true)
    }
    const Submit =() =>{
        let newVariable = [{commands:commands}]
        console.log("newVariable", newVariable)
        setDisplay(false)
        setCommands("")
        setNewVariabless(...newVariable)
    }

    return(
        <div>
          <button onClick={likeFunc}>Like</button>{count}<br/>
          <button onClick={comandDunc}>Command</button>
         <br/>
         {display ?
         <div>
         <input value={commands} placeholder="enter ur commands" onChange={(e) => setCommands(e.target.value)} />
         <button onClick={Submit}>Submit</button>
         </div> 
         : ""}

        {/* <p>{newState?.commands}</p><br/> */}
        </div>
    );
}

export default Commands;